import Vue from 'vue'
import Router from 'vue-router'
import index  from '@/view/index.vue'
import spellGroup  from '@/view/spellGroup.vue'
import myOrder  from '@/view/myOrder.vue'
import userCenter  from '@/view/userCenter.vue'
import cart  from '@/view/cart.vue'
import pay  from '@/view/pay.vue'
import login  from '@/view/login.vue'
import shopDetail  from '@/view/shopDetail.vue'
import Coupons  from '@/view/coupons.vue'
import payCoupon  from '@/view/payCoupon.vue'
import goCoupons  from '@/view/goCoupons.vue'
import register  from '@/view/register.vue'
import spellPay  from '@/view/spellPay.vue'
import groupDetail  from '@/view/groupDetail.vue'
import collection  from '@/view/collection.vue'



Vue.use(Router)

export default new Router({
  routes: [
    { path: '/', name: 'index',component: index},//
    {path: '/spellGroup',name: 'spellGroup',meta: {auth: true },component: spellGroup},//true
    {path: '/my/Order',name: 'myOrder',meta: {auth: true },component: myOrder},//
    {path: '/userCenter',name: 'userCenter',meta: {auth: true},component: userCenter},//
    {path: '/cart',name: 'cart',meta: {auth: true },component: cart},//
    {path: '/shopDetail/:id',name: 'shopDetail',component: shopDetail},//
    {path: '/login',name: 'login',component: login},//
    {path: '/register',name: 'register',component: register},//
    {path: '/pay', name: 'pay',meta: {auth: true },component: pay},//
    {path: '/coupons', name: 'Coupons',component: Coupons},//
    {path: '/payCoupon', name: 'payCoupon',component: payCoupon},//
    {path: '/goCoupons', name: 'goCoupons',component: goCoupons},//
    {path: '/spellPay', name: 'spellPay',component: spellPay},//
    {path: '/groupDetail', name: 'groupDetail',component: groupDetail},//
    {path: '/collection', name: 'collection',component: collection},//
  ]
})



