import * as func from "../function.js";

export default{
	getSpellList(states){ //
		return states.spellList.filter(vla=>{
			if(vla.spellState === 1){
				return vla;
			}
		})
	},
	
	getSpellGroupPeople(states){ //
		return states.spellList.map(vla=>{
			return vla;
		})
	},
	
	successSpell(states){ //
		return states.spellList.filter(vla=>{
			if(vla.spellState === 2){
				return vla;
			}
		})
	},
	failureSpell(states){//
		return states.spellList.filter(vla=>{
			if(vla.spellState === 3){
				return vla;
			}
		})
	},
	
	spellGroupLength(states){//
		return states.spellList.length;
	}
	
}


